.team .box {
  background: #ffffff;
  border-radius: 6px;
  border: 1px solid rgb(62, 28, 131, 0.1);
  padding: 2rem;
  transition: 0.5s;
}
.team .box:hover {
  box-shadow: 0 0 20px 0 rgb(112, 121, 138, 0.18);
  cursor: pointer;
}
.team .details {
  text-align: center;
  margin-top: 30px;
}
.team .img {
  width: 90px;
  height: 90px;
  margin: auto;
  position: relative;
}
.team img {
  width: 90px;
  height: 90px;
  border-radius: 50%;
}
.team .img i {
  color: #108be7;
  font-size: 17px;
  position: absolute;
  top: 35px;
  right: -10px;
}
.team .img::after {
  content: "";
  position: absolute;
  top: -15px;
  left: -15px;
  width: 110px;
  height: 110px;
  border: 5px solid #edf0f5;
  border-radius: 50%;
}
.team label {
  display: inline-block;
  margin-top: 20px;
}
.team i {
  color: grey;
  margin-right: 5px;
}
.team ul {
  display: inline-block;
  margin-top: 10px;
}
.team ul li {
  display: inline-block;
  width: 40px;
  height: 40px;
  line-height: 40px;
  align-items: center;
  background-color: #f4f5f7;
  border-radius: 50%;
  margin: 5px;
}
.team .button {
  margin-top: 20px;
}
.team .button i {
  color: white;
  font-size: 20px;
}

/* Responsive Design for Tablets */
@media screen and (max-width: 800px) {
  .team .box {
    padding: 1.5rem;
  }
  .team .img {
    width: 80px;
    height: 80px;
  }
  .team img {
    width: 80px;
    height: 80px;
  }
  .team .details {
    margin-top: 20px;
  }
  .team label {
    font-size: 14px;
  }
  .team ul li {
    width: 35px;
    height: 35px;
    line-height: 35px;
  }
  .team .button {
    margin-top: 15px;
  }
}

.box {
  transition: transform 0.2s, box-shadow 0.2s;
}

.box:hover {
  transform: scale(1.05); /* Slightly enlarge the card on hover */
  box-shadow: 0 4px 15px rgba(0, 0, 0, 0.2); /* Add shadow on hover */
}


/* Responsive Design for Mobile */
@media screen and (max-width: 480px) {
  .team .box {
    padding: 1rem;
  }
  .team .img {
    width: 70px;
    height: 70px;
  }
  .team img {
    width: 70px;
    height: 70px;
  }
  .team .details {
    margin-top: 15px;
  }
  .team label {
    font-size: 12px;
  }
  .team ul li {
    width: 30px;
    height: 30px;
    line-height: 30px;
  }
  .team .button {
    margin-top: 10px;
  }
}
